import React from 'react'
import '../Styles/Main.css'
import Logo from "../Assets/niri.png";
import "../Styles/main.js";
import 'bootstrap/dist/css/bootstrap.min.css';
import { useState } from 'react';
function Header() {

  const [isMobileNavOpen, setMobileNavOpen] = useState(false);

  const handleMobileNavToggle = () => {
    setMobileNavOpen((prevValue) => !prevValue);
  };


  // const handleDropdownToggle = (e) => {
  //   if (isMobileNavOpen) {
  //     e.preventDefault();
  //     const dropdownContent = e.target.nextElementSibling;
  //     dropdownContent.classList.toggle('dropdown-active');
  //   }
  // };


  return (

    
    <header id="header" className={"fixed-top d-flex align-items-center  ${isMobileNavOpen ? 'navbar-mobile' : ''}"}>

      <div className="container d-flex justify-content-between">

        <div className="logo">
          <h1>
            <a href="#hero"><img src={Logo} alt="" className="" />&nbsp;&nbsp;NIRI GLOBAL SERVICES</a>
          </h1>
        </div>

        <nav id="navbar" className="navbar">
          <ul>
            <li><a className="nav-link scrollto" href="#hero">Home</a></li>
            <li><a className="nav-link scrollto" href="#about">About</a></li>
            <li><a className="nav-link scrollto" href="#services">Services</a></li>
            <li><a className="nav-link scrollto " href="#portfolio">Portfolio</a></li>
            <li><a className="nav-link scrollto" href="#team">Team</a></li>
            <li><a className="nav-link scrollto" href="#contact">Contact</a></li>
          </ul>
          <i  className={`bi ${isMobileNavOpen ? 'bi-x' : 'bi-list'} mobile-nav-toggle`}
            onClick={handleMobileNavToggle}></i>
        </nav>

      </div>
    </header>
  )
}

export default Header