import React, { useState } from 'react';
import { Alert, AlertTitle } from '@mui/material';
import '../Styles/Main.css';
import Contact_card from './Contact_card';
import Map from './Map';

const Contact = () => {
  const [status, setStatus] = useState('Submit');
  const [showAlert, setShowAlert] = useState(false);
  const [errorAlert, setErrorAlert] = useState(false);
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    subject: '',
    message: '',
  });
  // setStatus("Sending...");
  const [inputErrors, setInputErrors] = useState({
    name: false,
    email: false,
    subject: false,
    message: false,
  });
  const [alertMessage, setAlertMessage] = useState(''); // Add this line
  const recipientEmail = 'info@niriglobal.com';

  const handleSendEmail = () => {
    const { name, email, subject, message } = formData;

    if (!name || !email || !subject || !message) {
      setAlertMessage('Please fill in all the fields before sending.');
      setErrorAlert(true);
      setInputErrors({
        name: !name,
        email: !email,
        subject: !subject,
        message: !message,
      });
      return;
    }

    const mailtoLink = `mailto:${recipientEmail}?subject=${encodeURIComponent(
      subject
    )}&body=${encodeURIComponent(`Name: ${name}\nEmail: ${email}\nMessage: ${message}`)}`;

    window.location.href = mailtoLink;
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value,
    });

    setInputErrors({
      ...inputErrors,
      [name]: false,
    });
  };
  return (
    <section id="contact" className="contact">
      <div className="container">
        <div className="section-title" data-aos="fade-up">
          <h2>Get in Touch with Us</h2>
        </div>
        <div className="row no-gutters justify-content-center" data-aos="fade-up">
          <Contact_card />
          <Map />
          <div className="col-lg-4 text-center"></div>
        </div>
        <div className="row mt-5 justify-content-center" data-aos="fade-up">
          <div className="col-lg-10">
            <form className="php-email-form">
              <div className="row">
                <div className="col-md-6 form-group">
                  <input
                    type="text"
                    name="name"
                    className={`form-control ${inputErrors.name ? 'is-invalid' : ''}`}
                    id="name"
                    placeholder="Your Name"
                    required
                    onChange={handleInputChange}
                  />
                  {inputErrors.name && <div className="invalid-feedback">Please fill out this field.</div>}
                </div>
                <div className="col-md-6 form-group mt-3 mt-md-0">
                  <input
                    type="email"
                    name="email"
                    className={`form-control ${inputErrors.email ? 'is-invalid' : ''}`}
                    id="email"
                    placeholder="Your Email"
                    required
                    onChange={handleInputChange}
                  />
                  {inputErrors.email && <div className="invalid-feedback">Please fill out this field.</div>}
                </div>
              </div>
              <div className="form-group mt-3">
                <input
                  type="text"
                  name="subject"
                  className={`form-control ${inputErrors.subject ? 'is-invalid' : ''}`}
                  id="subject"
                  placeholder="Subject"
                  required
                  onChange={handleInputChange}
                />
                {inputErrors.subject && <div className="invalid-feedback">Please fill out this field.</div>}
              </div>
              <div className="form-group mt-3">
                <textarea
                  className={`form-control ${inputErrors.message ? 'is-invalid' : ''}`}
                  name="message"
                  rows="5"
                  placeholder="Message"
                  id="message"
                  required
                  onChange={handleInputChange}
                ></textarea>
                {inputErrors.message && <div className="invalid-feedback">Please fill out this field.</div>}

              </div>
              <div className="my-3">
                {showAlert && (
                  <Alert severity="success" onClose={() => setShowAlert(false)}>
                    <AlertTitle>Success</AlertTitle>
                    Your message has been sent. Thank you!
                  </Alert>
                )}
                {errorAlert && (
                  <Alert severity="error" onClose={() => setErrorAlert(false)}>
                    <AlertTitle>Error</AlertTitle>
                    {alertMessage}
                  </Alert>
                )}
              </div>
              <div className="text-center">
                <button type="submit" onClick={handleSendEmail}>{status}</button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Contact;
