// ActionProvider.js
class ActionProvider {
    constructor(
      createChatBotMessage,
      setStateFunc,
    ) {
      this.createChatBotMessage = createChatBotMessage;
      this.setState = setStateFunc;
    }
  
    greet = () => {
      const message = this.createChatBotMessage("Hi, Sorry for this inconvenience, I'm currently in development mode. If there is any query, please contact us at 8806931555 or email us at hr@niriglobal.com.");
      this.addMessageToState(message);
    };
  
    greet1 = () => {
      const message = this.createChatBotMessage("I'm just a bot, but thanks for asking!");
      this.addMessageToState(message);
    };
  
    displayGoogleAnswer = (answer) => {
      const message = this.createChatBotMessage(answer);
      this.addMessageToState(message);
    };
  
    noGoogleResults = () => {
      const message = this.createChatBotMessage("I couldn't find any relevant information.");
      this.addMessageToState(message);
    };
  
    googleError = () => {
      const message = this.createChatBotMessage("I encountered an error while searching. Please try again later.");
      this.addMessageToState(message);
    };
  
    defaultResponse = () => {
      const message = this.createChatBotMessage("I'm sorry, I don't understand your question.");
      this.addMessageToState(message);
    };
  
    addMessageToState = (message) => {
      this.setState(prevState => ({
        ...prevState,
        messages: [...prevState.messages, message],
      }));
    };
  }
  
  export default ActionProvider;
  