import React from 'react'
import "../Styles/Main.css";
import "../Styles/main.js";
function faq() {
  return (
    <section id="faq" className="faq section-bg">
      <div className="container">

        <div className="section-title" data-aos="fade-up">
          <h2>Frequently Asked Questions</h2>
          <p>Find answers to commonly asked questions about .</p>
        </div>

        <div className="faq-list">
          <ul>
            <li data-aos="fade-up">
              <i className="bx bx-help-circle icon-help"></i> <a data-bs-toggle="collapse" className="collapse" data-bs-target="#faq-list-1">Where can I find information on new features in Niri Cloud?? <i className="bx bx-chevron-down icon-show"></i><i className="bx bx-chevron-up icon-close"></i></a>
              <div id="faq-list-1" className="collapse show" data-bs-parent=".faq-list">
                <p>
                  You can learn all about the latest features in Niri Cloud on the overview page. To find out what’s in the latest version of each Niri Cloud desktop app, check out each product’s page on niriglobal.com.
                </p>
              </div>
            </li>

            <li data-aos="fade-up" data-aos-delay="100">
              <i className="bx bx-help-circle icon-help"></i> <a data-bs-toggle="collapse" data-bs-target="#faq-list-2" className="collapsed">When does my membership begin? <i className="bx bx-chevron-down icon-show"></i><i className="bx bx-chevron-up icon-close"></i></a>
              <div id="faq-list-2" className="collapse" data-bs-parent=".faq-list">
                <p>
                  If you purchase your individual membership from a retailer or an online store, it begins when you enter your redemption code.

                  If you purchase a team membership through a reseller, your membership begins when you add seats to your account. </p>
              </div>
            </li>

            <li data-aos="fade-up" data-aos-delay="200">
              <i className="bx bx-help-circle icon-help"></i> <a data-bs-toggle="collapse" data-bs-target="#faq-list-3" className="collapsed">What happens to my benefits if I decide to stop my membership? <i className="bx bx-chevron-down icon-show"></i><i className="bx bx-chevron-up icon-close"></i></a>
              <div id="faq-list-3" className="collapse" data-bs-parent=".faq-list">
                <p>
                  You continue to have access to Niri Cloud member free benefits. And, if you saved your work to your computer,
                  you continue to have access to those files on your own device. You no longer have access to the Niri Cloud apps
                  or most of the services that are components of a Niri Cloud membership.</p>
              </div>
            </li>

            <li data-aos="fade-up" data-aos-delay="300">
              <i className="bx bx-help-circle icon-help"></i> <a data-bs-toggle="collapse" data-bs-target="#faq-list-4" className="collapsed">What’s included with a free Creative Cloud membership? <i className="bx bx-chevron-down icon-show"></i><i className="bx bx-chevron-up icon-close"></i></a>
              <div id="faq-list-4" className="collapse" data-bs-parent=".faq-list">
                <p>
                  A free niri Cloud membership gives you access to file syncing and sharing features and 2 GB of storage space.
                  You also get some free services and access to free trials of the Niri Cloud desktop application</p>
              </div>
            </li>

            <li data-aos="fade-up" data-aos-delay="400">
              <i className="bx bx-help-circle icon-help"></i> <a data-bs-toggle="collapse" data-bs-target="#faq-list-5" className="collapsed">Can I exchange my order instead of returning it? <i className="bx bx-chevron-down icon-show"></i><i className="bx bx-chevron-up icon-close"></i></a>
              <div id="faq-list-5" className="collapse" data-bs-parent=".faq-list">
                <p>
                  As a Niri Global customer you have the option of exchanging an eligible item instead of returning it for a refund.
                  You may be able to process an exchange yourself for certain eligible items in your Niri Global account.  </p>
              </div>
            </li>

          </ul>
        </div>

      </div>
    </section>
  )

}

export default faq