// MessageParser.js
import axios from 'axios';

class MessageParser {
  constructor(actionProvider, state) {
    this.actionProvider = actionProvider;
    this.state = state;
  }

  async parse(message) {
    const lowercase = message.toLowerCase();

    if (lowercase.includes('hello') || lowercase.includes('hi')) {
      this.actionProvider.greet();
    } else if (lowercase.includes('how are you')) {
      this.actionProvider.greet1();
    } else if (lowercase.startsWith('google')) {
      const searchQuery = message.replace('google', '').trim();
      try {
        const response = await axios.get(`https://cse.google.com/cse?cx=906978ff9cb8b46f1?q=${searchQuery}&key==AIzaSyAUOfIPV1o9cktPr0BOKxqIsHMzEQ_A4n8`);
        const searchResults = response.data.items;
        if (searchResults.length > 0) {
          const answer = searchResults[0].snippet;
          this.actionProvider.displayGoogleAnswer(answer);
        } else {
          this.actionProvider.noGoogleResults();
        }
      } catch (error) {
        console.error('Error fetching Google search results:', error);
        this.actionProvider.googleError();
      }
    } else {
      this.actionProvider.defaultResponse();
    }
  }
}

export default MessageParser;
