import React, { useState, useEffect } from 'react';
import "../Styles/Main.css";
import "../Styles/main.js";
import Logo from "../Assets/portfolio/portfolio-1.jpg";
import Logo1 from "../Assets/portfolio/portfolio-2.jpg";
import Logo2 from "../Assets/portfolio/portfolio-3.jpg";
import Logo3 from "../Assets/portfolio/portfolio-4.jpg";
import Logo4 from "../Assets/portfolio/portfolio-5.jpg";
import Logo5 from "../Assets/portfolio/portfolio-6.jpg";
import Logo6 from "../Assets/portfolio/portfolio-7.jpg";
import Logo7 from "../Assets/portfolio/portfolio-8.jpg";
import Logo8 from "../Assets/portfolio/portfolio-9.jpg";

const yourImagesArray = [
  { src: Logo, filter: '.filter-app', title: 'App 1', category: 'App' },
  { src: Logo1, filter: '.filter-web', title: 'Web 3', category: 'Web' },
  { src: Logo2, filter: '.filter-app', title: 'App 2', category: 'App' },
  { src: Logo3, filter: '.filter-card', title: 'Card 2', category: 'Card' },
  { src: Logo4, filter: '.filter-web', title: 'Web 2', category: 'Web' },
  { src: Logo5, filter: '.filter-app', title: 'App 3', category: 'App' },
  { src: Logo6, filter: '.filter-card', title: 'Card 1', category: 'Card' },
  { src: Logo7, filter: '.filter-card', title: 'Card 3', category: 'Card' },
  { src: Logo8, filter: '.filter-web', title: 'Web 3', category: 'Web' },
];

function Portfolio() {
  const [filter, setFilter] = useState('*');
  const [imagesVisible, setImagesVisible] = useState(false);

  const handleFilterClick = (newFilter) => {
    setImagesVisible(false);
    setTimeout(() => {
      setFilter(newFilter);
      setImagesVisible(true);
    }, 500);
  };

  useEffect(() => {
    setImagesVisible(true);
  }, []);

  return (
    <section id="portfolio" className="portfolio section-bg">
      <div className="container">
        <div className="section-title" data-aos="fade-up">
          <h2>Portfolio</h2>
          <p>It takes great pains to benefit. His needs result from something that actually drives him away. Let them be what they want. Anyone whom anyone desires. And no one who hinders receives the others. Because he should flee in this office of convenience, which is here.</p>
        </div>

        <div className="row" data-aos="fade-up">
          <div className="col-lg-12 d-flex justify-content-center">
            <ul id="portfolio-flters">
              <li
                onClick={() => handleFilterClick('*')}
                className={filter === '*' ? 'filter-active' : ''}
              >
                All
              </li>
              <li
                onClick={() => handleFilterClick('.filter-app')}
                className={filter === '.filter-app' ? 'filter-active' : ''}
              >
                App
              </li>
              <li
                onClick={() => handleFilterClick('.filter-card')}
                className={filter === '.filter-card' ? 'filter-active' : ''}
              >
                Card
              </li>
              <li
                onClick={() => handleFilterClick('.filter-web')}
                className={filter === '.filter-web' ? 'filter-active' : ''}
              >
                Web
              </li>
            </ul>
          </div>
        </div>
        <div className="row portfolio-container" data-aos="fade-up">
          {yourImagesArray.map((image, index) => {
            if (filter === '*' || image.filter === filter) {
              return (
                <div key={index} className={`col-lg-4 col-md-6 portfolio-item ${image.filter}`} style={{ opacity: imagesVisible ? 1 : 0, transform: imagesVisible ? 'translateY(0)' : 'translateY(20px)', transition: 'opacity 0.5s ease-in-out, transform 0.5s ease-in-out' }}>
                  <div className="portfolio-wrap">
                    <img src={image.src} className="img-fluid" alt={`Image ${index}`} />
                    <div className="portfolio-info">
                      <h4>{image.title}</h4>
                      <p>{image.category}</p>
                      <div className="portfolio-links">
                        <a href={image.src} data-gallery="portfolioGallery" className="portfolio-lightbox" title={image.title}></a>
                        <a href="#" title="More Details"><i className="bx bx-link"></i></a>
                      </div>
                    </div>
                  </div>
                </div>
              );
            }
            return null;
          })}
        </div>
      </div>
    </section>
  );
}

export default Portfolio;
