import React from 'react'
import "../Styles/Main.css";
import "../Styles/main.js";
import Logo from "../Assets/features-1.png";
import Logo1 from "../Assets/features-2.png";
import Logo2 from "../Assets/features-3.png";
import Logo3 from "../Assets/features-4.png";
function feature() {
  return (
    <section id="features" className="features">
      <div className="container">

        <div className="row">
          <div className="col-lg-4 mb-5 mb-lg-0" data-aos="fade-right">
            <ul className="nav nav-tabs flex-column">
              <li className="nav-item">
                <a className="nav-link active show" data-bs-toggle="tab" href="#tab-1">
                  <h4>REASONABLE PRICE</h4>
                  <p>Our projects look like you payed more than you actully did. No overpay for the company's brand.</p>
                </a>
              </li>
              <li className="nav-item mt-2">
                <a className="nav-link" data-bs-toggle="tab" href="#tab-2">
                  <h4>FAST</h4>
                  <p>over the years of experience, we have picked the most suitable technologies and solutions for each type of projects.</p>
                </a>
              </li>
              <li className="nav-item mt-2">
                <a className="nav-link" data-bs-toggle="tab" href="#tab-3">
                  <h4>UP-TO-DATE</h4>
                  <p>within 6 weeks after the release, we will supervise the relevance of the technologies used in the project.</p>
                </a>
              </li>
              <li className="nav-item mt-2">
                <a className="nav-link" data-bs-toggle="tab" href="#tab-4" >
                  <h4>LONG-LASTING</h4>
                  <p>You get only a sucessful product but also a reliable business partner. We keep in touch with our customers</p>
                </a>
              </li>
            </ul>
          </div>
          <div className="col-lg-7 ml-auto" data-aos="fade-left">
            <div className="tab-content">
              <div className="tab-pane active show" id="tab-1">
                <figure>
                  <img src={Logo} alt="" className="img-fluid" />
                </figure>
              </div>
              <div className="tab-pane" id="tab-2">
                <figure>
                  <img src={Logo1} alt="" className="img-fluid" />
                </figure>
              </div>
              <div className="tab-pane" id="tab-3">
                <figure>
                  <img src={Logo2} alt="" className="img-fluid" />
                </figure>
              </div>
              <div className="tab-pane" id="tab-4">
                <figure>
                  <img src={Logo3} alt="" className="img-fluid" />
                </figure>
              </div>
            </div>
          </div>
        </div>

      </div>
    </section>
  )
}

export default feature